import React from 'react'
import { Link } from 'react-router-dom'

class Gallery extends React.Component{
  constructor(){
    super();
    this.state = {
      items : [
        {
          src : 'gallery/plot_2.png',
          label : '2D Curve Generation',
          description : 'Place control points to generate curves using various algorithms',
          link : 'plot'
        },
        {
          src : 'backgroundred.png',
          label : 'Slide 2',
          description : 'slide 2',
          link : 'plot'
        },
        {
          src : 'background.jpg',
          label : 'Slide 3',
          description : 'slide 3',
          link : 'plot'
        } 
      ],
      index : 0
    }
    this.renderCarouselItems = this.renderCarouselItems.bind(this)
  }

  onCarouselClick = (event) => {
    var id = event.target.id
    var index = this.state.index
    
    if (id === 'next'){
      index++ 
    }
    if (id === 'prev'){
      index--
    }
    console.log(index)
    index = index % this.state.items.length
    console.log(index)
    this.setState({"index" :  index})
  }
  
    renderCarouselItems(){
      var carItems = []
      var clsName = "carousel-item active"
      
      this.state.items.forEach(
        (element, index) => {
          carItems.push(
            <Link key={index} className={clsName} to={`/${element.link}`}>
              <img src={element.src} className="d-block w-100 img-filter"/>
              <div className="carousel-caption d-block bg-dark">
                <h5>{element.label}</h5>
                <p>{element.description}</p>
              </div>
            </Link>
          )
          clsName="carousel-item"
        }
      )
      return carItems
    }

    render(){
        return (
            <div className="container-fluid">
              <div className="row fadein py-4 mx-lg-5">
                <div className="carousel slide offset-md-2 col-md-8"  data-interval={false} onChange={(event) => {console.log(event)}}  id="carouselExampleCaptions" data-ride="carousel">
                  {/* <ol className="carousel-indicators">
                    <li data-target="#carouselExampleCaptions" data-slide-to="0" className="active"></li>
                    <li data-target="#carouselExampleCaptions" data-slide-to="1"></li>
                    <li data-target="#carouselExampleCaptions" data-slide-to="2"></li>
                  </ol> */}
                  <div className="carousel-inner">
                    {this.renderCarouselItems()}
                  </div>

                  <a id='prev' className="carousel-control-prev" href="#carouselExampleCaptions" 
                  role="button" data-slide="prev" onClick={this.onCarouselClick}>
                    <span className="carousel-control-prev-icon" aria-hidden="true"></span>
                    <span className="sr-only">Previous</span>
                  </a>

                  <a id='next' className="carousel-control-next" href="#carouselExampleCaptions" 
                  role="button" data-slide="next" onClick={this.onCarouselClick}>
                    <span className="carousel-control-next-icon" aria-hidden="true"></span>
                    <span className="sr-only">Next</span>
                  </a>
                </div>
                {/* <div className="col-md-4 d-block">
                  <h5>{this.state.items[this.state.index].label}</h5>
                  <p>{this.state.items[this.state.index].description}</p>
                </div> */}
              </div>
            </div>
        )
    }
}

export default Gallery;